// edit.js
.slider-slide {
    height: 300px;
    padding: 20px;
    background-size: cover; /* Ensure background images cover the area */
    background-position: center; /* Center background images */
    background-repeat: no-repeat;
    overflow: hidden; /* Prevents overflow */
    text-align: center; 
}
